import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

// "use buttons components"
import HostButtonsBlock from './components/host-buttons-block';
import ParticipantsButtonsBlock from './components/participants-buttons-block';

// Intro screen
import PopupIntro from './components/popup-intro';

// Virtual clicker component
import VirtualClickerComponent from './components/virtual clicker/clicker-container';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      // clicker, web, web-disconnect
      call2Popup: "none",
      VirtualClicker: false,
      hostButtonsStates: {
        "bttn1": false,
        "bttn2": false,
        "bttn3": false,
        "bttn4": false,
      },
      participantsButtonsStates: {
        "bttn1": false,
        "bttn2": false,
        "bttn3": false
      },

    };

    // This binding is necessary to make `this` work in the callback
    this.actionFromHostBttns = this.actionFromHostBttns.bind(this);
    this.updatePopupState = this.updatePopupState.bind(this);
    this.requestFromParticipantsBlock = this.requestFromParticipantsBlock.bind(this);
    // this.theCall.bind(this);
  };

  actionFromHostBttns (arg) {

    if (arg === "clicker") {

      // ES6 way
      setTimeout(() => {
        this.setState({call2Popup: arg});
      }, 3000)

      //// old-way
      // setTimeout(function () {
      //   this.setState({call2Popup: arg});
      // }.bind(this), 2000)

    }

    if (arg === "web") {
      var randomdrop = Math.floor(Math.random()*2);

      if (randomdrop === 0) {
        // this.setState({call2Popup: arg});
        setTimeout(() => {
          this.setState({call2Popup: arg});
        }, 3000)
      }
      else {
        // this.setState({call2Popup: "web-disconnect"});
        setTimeout(() => {
          this.setState({call2Popup: "web-disconnect"});
        }, 3000)
      }
    }

    if (arg === "virtual") {
      this.state.VirtualClicker && !this.state.participantsButtonsStates.bttn3 ? this.setState({VirtualClicker: false}) : this.setState({VirtualClicker: true});
      // this.state.hostButtonsStates.bttn4 ? this.setState({hostButtonsStates["bttn4"]: false}) : this.setState({hostButtonsStates["bttn4"]: false});

      // Если у вас есть другие переменные в abc
      let newObj = this.state.hostButtonsStates;
      newObj.bttn4 = this.state.hostButtonsStates.bttn4 ? false : true;
      this.setState({hostButtonsStates: newObj});

      // console.log(this.state.hostButtonsStates);


        // this.setState({hostButtonsStates: {...this.state.hostButtonsStates, "bttn4": false}})
      // this.setState({stateVirtualClicker: true});
      // setTimeout(() => {
      //   this.state.VirtualClicker ? this.setState({VirtualClicker: false}) : this.setState({VirtualClicker: true});
      // }, 3000)
      // console.log("bobobo");
      // setTimeout(function () {
      //   // this.state.VirtualClicker ? this.setState({VirtualClicker: false}) : this.setState({VirtualClicker: true});
      //   this.state.hostButtonsStates.bttn4 ? this.setState({hostButtonsStates: {"bttn4": false}}) : this.setState({hostButtonsStates: {"bttn4": true}})
      // }.bind(this), 2000)
    }

  }

  requestFromParticipantsBlock (button) {
    switch (button) {
      case "clicker":
      // неправильно
      // this.state.participantsButtonsStates.bttn1 ? this.setState({participantsButtonsStates: {bttn1: false}}) : this.setState({participantsButtonsStates: {bttn1: true}});
    
      break;
      case "web":
      // this.state.participantsButtonsStates.bttn2 ? this.setState({"bttn2": false}) : this.setState({"bttn2" : true});
      break;
      case "virtual":
      this.state.VirtualClicker && !this.state.hostButtonsStates.bttn4 ? this.setState({VirtualClicker: false}) : this.setState({VirtualClicker: true});
      // неправильно
      // this.state.participantsButtonsStates.bttn3 ? this.setState({"bttn3": false}) : this.setState({"bttn3" : true});
      let newObj = this.state.participantsButtonsStates;
      newObj.bttn3 = this.state.participantsButtonsStates.bttn3 ? false : true;
      this.setState({participantsButtonsStates: newObj});
      break;
      default:
      console.log("error with argument at requestFromParticipantsBlock switch-function at main App.js")
    }
    // console.log("requestFromParticipantsBlock with arg: " + button)
  }

  // this function is linked with closing pop-up
  updatePopupState (arg, callerElement) {
    // console.log("argument " + arg + " & caller is: " + callerElement);
    this.setState({call2Popup: "none",
      hostButtonsStates: {
      "bttn1": false,
      "bttn2": false,
      "bttn3": false,
      "bttn4": false
    }});


    // switch (arg) {
    //   case "web":
    //     // console.log("web");
    //     this.setState({call2Popup: arg});
    //     break;
    //   case "click":
    //     // console.log("click");
    //     this.setState({call2Popup: arg});
    //     break;
    //   default:
    //   console.log("achtung");
    // }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Eduware Inc. LivePoll App. v2</h1>
        </header>


          {/* Initial intro screen */}
          <div>
            {/*{this.state.VirtualClicker ? <VirtualClickerComponent /> : console.log("no Virtual clicker yet")} */}
            {this.state.VirtualClicker ? <VirtualClickerComponent host={this.state.hostButtonsStates.bttn4} participant={this.state.participantsButtonsStates.bttn3} /> : null}
            {/* <VirtualClickerComponent call2ShowClicker={this.state.call2VirtualClicker}/> */}

            <HostButtonsBlock call2ButtonsFromParent={this.state.hostButtonsStates} callToParentScreen={this.actionFromHostBttns}/>

            <ParticipantsButtonsBlock callToParentComponent={this.requestFromParticipantsBlock} /> 

            <PopupIntro call2Show={this.state.call2Popup} callToParentScreen={this.updatePopupState} />
          </div>

      </div>

    );
  }
}

export default App;



